const reload = document.getElementById('reload');

reload.addEventListener('click', () => { 
    location.reload();
});

const variableA = document.querySelector('#a')
const variableB = document.querySelector('#b')
const variableC = document.querySelector('#c')
const btn = document.querySelector('#btn')
const resultadoFinal = document.querySelector('.resultadoFinal')

let a = 0;
let b = 0; 
let c = 0;

variableA.addEventListener('input', e=>{
    a = parseInt(e.target.value); 
})
variableB.addEventListener('input', e=>{
    b = parseInt(e.target.value);  
})
variableC.addEventListener('input', e=>{
    c = parseInt(e.target.value);
})


function ecuacion() {
    let x1 = Math.round((-b+ Math.sqrt((Math.pow(b,2)-4*a*c))) /(2*a));
    let x2 = Math.round((-b- Math.sqrt((Math.pow(b,2)-4*a*c))) /(2*a));

    let P1 = Math.round((-b));
    let P2 =Math.round((Math.pow(b,2)));
    let P3 = Math.round((-4*a*c))
    let P4 =Math.round((2*a))
    let P5 = Math.round((Math.sqrt((Math.pow(b,2)-4*a*c))))
    const P6 =Math.round((-b- Math.sqrt((Math.pow(b,2)-4*a*c))))
    
    if (isNaN(P6)) {
        const resultado = document.createElement('DIV')
        resultado.innerHTML = `<p class="resultadoIncorrecto">El Resultado no pertenece a los numeros reales</p>`
        const div = document.createElement('DIV')
        div.appendChild(resultado)
        resultadoFinal.appendChild(div)
   }else{
    const resultado = document.createElement('DIV')
    resultado.innerHTML = 
    `<div class="resultado">
            <h4 class="text-aling">A = ${a} B = ${b} C = ${c}</h4>

            <h3>Resultado:</h3>
            <p>X1=${x1}</p>
            <p>X2=${x2}</p>
            <h3>ProcedimientoX1:</h3>
            <p>X1= ${P1}+√(${P2}+(${P3}))/${P4}</p>
            <p>X1= (${P1}+${P5})/${P4}</p>
            <p>X1= ${P6}/${P4}</p>
            <p>X1= ${x1}</p>
            <h3>ProcedimientoX2:</h3>
            <p>X2= ${P1}-√(${P2}+(${P3}))/${P4}</p>
            <p>X2= (${P1}-${P5})/${P4}</p>
            <p>X2= ${P6}/${P4}</p>
            <p>X2= ${x2}</p>
            
            <h4 class="text-aling">Conjunto solución S{${x1} ${x2}}</h4>
        </div>`;

    const div = document.createElement('DIV')
    div.appendChild(resultado)
    resultadoFinal.appendChild(div)

   }
}

btn.addEventListener('click',()=>{
    resultadoFinal.textContent = ''
    ecuacion()
});


